import React, { Component } from 'react';
import { Form, Button, Input } from 'reactstrap';

class Contact extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (
            <div className="container" id="contactDiv">
                <div className="row contactContainer">
                    <div className="col-4">
                        <h4 className="text-center">Say Hello.</h4>
                        <Form>
                            <Input className="contactInput" type="name" name="name" id="nameContact" placeholder="Full Name" />
                            <Input className="contactInput" type="email" name="email" id="emailContact" placeholder="Your Email Address" />
                            <Input className="inputText" type="textarea" name="text" id="textContact">
                                Write Your Message Here.
                            </Input>
                            <div className="text-center">
                            <Button  id="sendButton">Submit</Button>
                            </div>
                        </Form>
                    </div>
                    <div className="col-8">
                        
                    </div>
                </div>
            </div>
        );
    }
}

export default Contact;