import React, { Component } from 'react'
import {
    Card, CardText, CardBody,
    CardTitle, CardSubtitle, Jumbotron
} from 'reactstrap';
import portfolioImg from '../img/portfolioImg.jpg';
import logo from '../img/logo.png';


class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (
            <div>
                <Jumbotron className="heroBanner" fluid>
                    <h1 className="text-center">
                        <div>
                            <img className="logo" src={logo} alt="Christopher John Roman Logo" />
                        </div>
                    </h1>
                </Jumbotron>

                {/* About Me */}
                <div className="container">

                    {/* Divider */}
                    <div className="Divider" />

                    {/* About Me Row */}
                    <div className="row mb-3">
                        <div className="col-4 offset-2">
                            <div>
                                <img className="portfolioImg" src={portfolioImg} alt="portfolio img" />
                            </div>
                        </div>
                        <div className="col-4">
                            <h2>
                                About Me
                            </h2>
                            <br />
                            <p>
                                Front End Developer with a background in design. Having a background in culinary, creation and design are things that spark my passion.
                            </p>
                            <p>
                                My technical experience includes JavaScript, HTML/CSS/SCSS, React, Node, Npm, and Bootstrap. I'll be working on my photography or whipping up a recipe in the Kitchen.
                            </p>
                        </div>
                    </div>

                    {/* Divider */}
                    <div className="Divider" />

                    {/* Skill Information Row */}
                    <div className="row">
                        <div className="col-4 ml-auto">
                            <Card className="cardHover">
                                <CardBody>
                                    <h3 className="text-center"><i className="fa fa-code" /></h3>
                                    <CardTitle tag="h5" className="text-center mt-3">Frontend</CardTitle>
                                    <CardSubtitle tag="h6" className="text-center m-1 px-2">Simplicity, I believe that modern websites focus on minimal design and intuitive features.</CardSubtitle>
                                    <CardText className="mt-3">
                                        <ul>
                                            <li>React</li>
                                            <li>HTML</li>
                                            <li>CSS/SASS</li>
                                            <li>JavaScript</li>
                                            <li>Bootstrap/Zurb</li>
                                        </ul>
                                    </CardText>
                                </CardBody>
                            </Card>
                        </div>
                        <div className="col-4 mr-auto">
                            <Card className="cardHover">
                                <CardBody>
                                    <h3 className="text-center
                                    "><i className="fa fa-edit" /></h3>
                                    <CardTitle tag="h5" className="text-center mt-3">Design</CardTitle>
                                    <CardSubtitle tag="h6" className="text-center m-1 px-2">Design is understanding the audience and I enjoy the creation process that encaptures that.</CardSubtitle>
                                    <CardText className="mt-3">
                                        <ul>
                                            <li>XD/BalsamiQ</li>
                                            <li>Illustrator</li>
                                            <li>InDesign</li>
                                            <li>Photoshop</li>
                                        </ul>
                                    </CardText>
                                </CardBody>
                            </Card>
                        </div>
                    </div>
                    {/* Divider */}
                    <div className="Divider" />

                    <div className="row">
                        <div className="col">
                            <h2 className="text-center">Photography</h2>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Main;